@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
  font-family: "roboto", "sans serif";
}
body {
  background: url(/todo-app/static/media/todo-img.6f144ff8.jpg);
  background-position: fixed;
}
img {
  max-width: 100%;
}
#root {
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.task {
  background-color: #17367e;
  padding: 2rem 0;
  border-radius: 10px;
}
.task + .task {
  margin-top: 2rem;
}
.task > .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.task p {
  font-size: 1.6rem;
}
.bottom {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.bottom button {
  font-size: 1.5rem;
  width: 45%;
}
.checked {
  opacity: 0.7;
}
.btn--check {
  background-color: #000;
}
.btn--delete {
  background-color: red;
  color: #fff;
}
@media (min-width: 500px) {
  .task > .container {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
  .task p {
    width: 60%;
  }
  .bottom {
    margin: 0;
    flex-direction: column;
    width: 30%;
  }
  .bottom button {
    width: 100%;
  }
  .btn--delete {
    margin: 1rem 0 0 0;
  }
}
@media (min-width: 1000px) {
  .bottom {
    flex-direction: row;
    width: 35%;
  }
  .bottom button {
    width: 45%;
  }
  .btn--delete {
    margin: 0;
  }
}

.container {
  width: 90%;
  margin: 0 auto;
}
.todo {
  border-radius: 10px;
  padding: 2rem 0;
  color: #fff;
  font-size: 2rem;
  width: 90%;
  margin: 2rem 0;
}
.todo button {
  padding: 1rem 0.5em;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-family: inherit;
  color: inherit;
}
.todo button:hover {
  opacity: 0.7;
}
.top {
  display: flex;
  flex-direction: column;
}
.top input {
  border: none;
  padding: 1rem;
  border-radius: 5px;
  font-family: inherit;
  font-size: 2rem;
}
.top button {
  margin: 1rem 0 0 0;
  background-color: #3de300;
  font-size: 2rem;
}
.no-elements {
  background-color: #f2f3f5;
  border: 1px solid #ccc;
  margin-top: 2rem;
  border-radius: 5px;
  color: #888;
  padding: 1.5rem 0;
  font-size: clamp(1.5rem, 0.5rem + 3vw, 3rem);
  text-align: center;
}
.no-elements > .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.empty-state-img {
  width: 70%;
  margin: 0 auto;
  margin-bottom: 2rem;
}
.tasks {
  margin-top: 2rem;
}
@media (min-width: 500px) {
  .top {
    flex-direction: row;
    justify-content: space-between;
  }
  .top input {
    width: 60%;
  }
  .top button {
    width: 30%;
    margin: 0;
  }
}

